import { Button, Cell, Field } from "@taroify/core"
import { MusicOutlined, SmileOutlined, WarningOutlined } from "@taroify/icons"
import * as React from "react"
import { useState } from "react"
import Block from "../../../components/block"
import Page from "../../../components/page"
import "./index.scss"

function BasicField() {
  const [value, setValue] = useState("")
  return (
    <Cell.Group inset>
      <Field
        value={value}
        label=""
        placeholder=""
        onChange={(e) => setValue(e.detail.value)}
      />
    </Cell.Group>
  )
}

function CustomField() {
  const [text, setText] = useState("")
  const [idcard, setIdcard] = useState("")
  const [number, setNumber] = useState("")
  const [digit, setDigit] = useState("")
  const [password, setPassword] = useState("")
  return (
    <Cell.Group inset>
      <Field
        value={text}
        label=""
        placeholder=""
        onChange={(e) => setText(e.detail.value)}
      />
      <Field
        value={idcard}
        label=""
        type="idcard"
        placeholder=""
        onChange={(e) => setIdcard(e.detail.value)}
      />
      <Field
        value={number}
        label=""
        type="number"
        placeholder=""
        onChange={(e) => setNumber(e.detail.value)}
      />
      <Field
        value={digit}
        label=""
        type="digit"
        placeholder=""
        onChange={(e) => setDigit(e.detail.value)}
      />
      <Field
        value={password}
        label=""
        type="password"
        placeholder=""
        onChange={(e) => setPassword(e.detail.value)}
      />
    </Cell.Group>
  )
}

function DisabledField() {
  return (
    <Cell.Group inset>
      <Field label="" placeholder="" readonly />
      <Field label="" placeholder="" disabled />
    </Cell.Group>
  )
}

function IconField() {
  const [value2, setValue2] = useState("")

  return (
    <Cell.Group inset>
      <Field
        label=""
        icon={<SmileOutlined />}
        rightIcon={<WarningOutlined />}
        placeholder=""
      />
      <Field
        value={value2}
        label=""
        icon={<MusicOutlined />}
        clearable
        placeholder=""
        onChange={(e) => setValue2(e.detail.value)}
      />
    </Cell.Group>
  )
}

function ErrorField() {
  return (
    <Cell.Group inset>
      <Field required error label="" placeholder="" />
      <Field required error label="" placeholder="" message="" />
    </Cell.Group>
  )
}

function ButtonField() {
  return (
    <Cell.Group inset>
      <Field align="center" label="" placeholder="">
        <Field.Button>
          <Button size="small" color="primary">
            
          </Button>
        </Field.Button>
      </Field>
    </Cell.Group>
  )
}

export default function FieldDemo() {
  return (
    <Page title="Field " className="field-demo">
      <Block title="">
        <BasicField />
      </Block>
      <Block title="">
        <CustomField />
      </Block>
      <Block title="">
        <DisabledField />
      </Block>
      <Block title="">
        <IconField />
      </Block>
      <Block title="">
        <ErrorField />
      </Block>
      <Block title="">
        <ButtonField />
      </Block>
    </Page>
  )
}
